<template>
  <div>
    <button class="discordLogin" v-on:click="discordLogin">Login with Discord</button>
  </div>
</template>

<script>
export default {
  methods: {
    discordLogin: function (event) {
      window.location.href = '/auth/discord'
    }
  }
}
</script>



