import Vue from 'vue'
import VueRouter from 'vue-router'
import VueResource from 'vue-resource'
import Login from './components/Login.vue'
import Home from './components/Home.vue'

Vue.use(VueRouter)
Vue.use(VueResource)

const routes = [
  { path: '/login', component: Login },
  { path: '/', component: Home, meta: { requiresAuth: true } }
]
const router = new VueRouter({
  routes
})

router.beforeEach((to, from, next) => {
  if (to.meta.requiresAuth) { // check the meta field
    if (this.isLoggedIn) { // check if the user is authenticated
      next() // the next method allow the user to continue to the router
    } else {
      next('/login') // Redirect the user to the main page
    }
  } else {
    next()
  }
})

new Vue({
  router,
  computed: {
    isLoggedIn: function () {
      return false
    }
  }
}).$mount('#app')



